<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/friendly/list"  @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>设置为启用，前台可显示友情链接地址</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px" @submit.native.prevent>
        <el-form-item label="名称" prop="linkName" :error='errorMessage.linkName'>
           <el-input  :autofocus="true" ref="first" v-model="formData.linkName"></el-input>
        </el-form-item>
        <el-form-item label="URL" prop="linkUrl"  :error='errorMessage.linkUrl' >
          <el-input v-model="formData.linkUrl"></el-input>
        </el-form-item>
        <el-form-item label="是否启用" prop="isEnable" >
          <cms-switch  v-model="formData.isEnable"></cms-switch>
        </el-form-item>
        <el-form-item label="Logo" prop="linkLogo" :error='errorMessage.linkLogo' class="brands-logo">
         <cms-picture-zone  @change="getLinkLogo" v-if="status"></cms-picture-zone>
        </el-form-item>
        <el-form-item label="排序值" prop="sortNum" :error='errorMessage.sortNum'>
          <el-input v-model.number="formData.sortNum" maxlength="2"></el-input>
        </el-form-item>
        <el-form-item label="备注信息" prop="remark">
          <el-input type="textarea" v-model="formData.remark" maxlength="200"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
         <cms-button @click="saveData(false)"  v-color >保存并新增</cms-button>
         <cms-button @click="saveData(true)"  v-color >保存</cms-button>
         <cms-button type='rest' @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { friendLinkSave } from '@/api/setting/friendlyLink.js'

var defaultData = {
  'linkName': '',
  'linkUrl': '',
  'isEnable': true,
  'linkLogo': '',
  'enabled': '',
  'sortNum': 10,
  'remark': ''
}
export default {
  name: 'friendlySave',
  mixins: [form],
  data() {
    return {
      formData: Object.assign({}, defaultData),
      rules: {// 新增规则
        linkName: [rules.required('请填写名称'), rules.len(1, 150)],
        linkUrl: [rules.required('请填写URL'), rules.len(1, 150), rules.url()],
        linkLogo: [rules.required('必须选择一张图片', 'change')],
        sortNum: [rules.required('请填写排序值'), rules.number()],
        remark: [rules.len(1, 200)]
      }
    }
  },
  methods: {
    getLinkLogo(items) {
      if (items) {
        this.formData.linkLogo = items[0].id
      } else {
        this.formData.linkLogo = ''
      }
      this.$refs.form.validateField('linkLogo')
    },
    saveData(state) {
      this.saveForm(friendLinkSave, this.formData, state, '/friendly/list', 'list')
    }
  },
  activated() {
    this.formData = Object.assign({}, defaultData)
    this.destroyed()
  }
}
</script>




